<template>
  <div class="home">
      <header class="home__header">
          <div class="flex">
              <div class="me" id="text" data-text='["CODER LI", "A Front-end Developer"]'></div><span class="mark"></span>
          </div>
          
          
          <div class="atwood">{{Atwood}}</div>
      </header>

      <NavArticle :articles="articles" :page="page" @change="paginationChange" :count="count" />

      <NavFooter />
  </div>
</template>

<script>
import NavFooter from '@/components/NavFooter.vue';
import NavArticle from '@/components/NavArticle';
import { getArticles } from '@/api/http';
import '@/styles/home.css';
export default {
    components: {
        NavFooter,
        NavArticle
    },
    data() {
        return {
            articles: null,
            page: {
                offset: 1,
                size: 5
            },
            count: 0,
            Atwood: "任何可以使用JavaScript來編寫的應用，最終也會由JavaScript編寫 —— Atwood定律",
        }
    },
    methods: {
        async paginationChange(e) {
            this.page = e;
            let res = await getArticles(this.page.offset, this.page.size);
            this.articles = res.data[0];
        },
        typeMe() {
            const textEl = document.querySelector("#text");
            const texts = JSON.parse(textEl.getAttribute("data-text"));
            let index = 0;
            let charIndex = 0;
            let delta = 500;

            let start = null;
            let isDeleting = false;

            function type(time) {
                window.requestAnimationFrame(type);
                if(!start) start = time;
                let progress = time - start;

                if(progress > delta) {
                    let text = texts[index];
                    if(!isDeleting) {
                        textEl.innerHTML = text.slice(0, ++charIndex);
                        delta = 500 - Math.random() * 400;
                    } else {
                        textEl.innerHTML = text.slice(0, charIndex--);
                    }

                    start = time;

                    if(charIndex === text.length) {
                        isDeleting = true;
                        delta = 200;
                        start = time + 1200;
                    }

                    if(charIndex < 0) {
                        isDeleting = false;
                        start = time + 200;
                        index = ++index % texts.length;
                    }
                }
            }
            window.requestAnimationFrame(type);
        },
        // type(time) {

        // }
    },
    async created() {

        let res = await getArticles(0,10);
        this.articles = res.data[0]
        this.count = res.data[1][0].c
    },
    mounted() {
        this.typeMe()
    }
}
</script>

