<template>
    <div class="login">
        <div class="login-container">
            <ul>
                <li
                    v-for="(log, index) in logForm"
                    :key="index"
                    @click="fnIndex(index)"
                    :class="{ active: index === logCur }"
                >
                    {{ log.title }}
                </li>
            </ul>
            <div class="log" v-if="logCur == 0">
                <div class="form-control">
                    <label for="username">用戶名</label>
                    <input
                        type="text"
                        id="username"
                        placeholder="請輸入用戶名"
                        v-model="loginData.username"
                        @input="usernameCheck"
                    />
                    <small v-if="errorMsg[0].status">{{
                        errorMsg[0].username
                    }}</small>
                </div>
                <div class="form-control">
                    <label for="password">密碼</label>
                    <input
                        type="password"
                        id="password"
                        placeholder="請輸入密碼"
                        v-model="loginData.password"
                        @input="passwordCheck"
                    />
                    <small v-if="errorMsg[1].status">{{
                        errorMsg[1].password
                    }}</small>
                </div>
                <button @click="logIn">提交</button>
            </div>
            <div class="register" v-if="logCur == 1">
                <div class="form-control">
                    <label for="username">用戶名</label>
                    <input
                        type="text"
                        id="username"
                        placeholder="請輸入用戶名"
                        v-model="loginData.username"
                        @input="usernameCheck"
                    />
                    <small v-if="errorMsg[0].status">{{
                        errorMsg[0].username
                    }}</small>
                </div>
                <div class="form-control">
                    <label for="password">密碼</label>
                    <input
                        type="password"
                        id="password"
                        placeholder="請輸入密碼"
                        v-model="loginData.password"
                        @input="passwordCheck"
                    />
                    <small v-if="errorMsg[1].status">{{
                        errorMsg[1].password
                    }}</small>
                </div>
                <div class="form-control">
                    <label for="password2">確認密碼</label>
                    <input
                        type="password"
                        id="password2"
                        placeholder="確認密碼"
                        v-model="loginData.password2"
                        @input="passwordCheck2"
                    />
                    <small v-if="errorMsg[1].status2">{{
                        errorMsg[1].password2
                    }}</small>
                </div>
                <button @click="registerFn">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
    data() {
        return {
            loginData: {
                username: "",
                password: "",
                password2: "",
            },
            errorMsg: [
                {
                    username: "用户名长度不能小於5位",
                    status: false,
                },
                {
                    password: "密碼長度不能小於5位",
                    password2: "兩次密碼不一致",
                    status: false,
                    status2: false,
                },
            ],
            logCur: 0,
            logForm: [
                {
                    title: "登錄",
                },
                {
                    title: "註冊",
                },
            ],
        };
    },
    methods: {
        ...mapActions(["login", "register"]),
        async logIn() {
            if (
                this.loginData.username.length >= 5 &&
                this.loginData.password.length >= 5
            ) {
                let data = {
                    username: this.loginData.username,
                    password: this.loginData.password,
                };
                await this.login(data);
                this.$router.push("/");
                this.$message.success("登入成功~");
            } else {
                this.$message.error("賬號或密碼有誤，請重新輸入~");
            }
        },
        async registerFn() {
            if (
                this.loginData.username.length >= 5 &&
                this.loginData.password.length >= 5 &&
                this.loginData.password2 === this.loginData.password
            ) {
                let data = {
                    username: this.loginData.username,
                    password: this.loginData.password,
                };
                await this.register(data);
                this.$router.push("/home");
                this.$message.success("註冊成功~");
            } else {
                this.$message.error("賬號或密碼有誤，請重新輸入~");
            }
        },
        fnIndex(index) {
            this.logCur = index;
            if (this.loginData.username || this.loginData.password) {
                this.loginData.username = "";
                this.loginData.password = "";
            }
        },
        usernameCheck() {
            if (this.loginData.username.length < 5) {
                this.errorMsg[0].status = true;
            } else {
                this.errorMsg[0].status = false;
            }
        },
        passwordCheck() {
            if (this.loginData.password.length < 5) {
                this.errorMsg[1].status = true;
            } else {
                this.errorMsg[1].status = false;
            }
        },
        passwordCheck2() {
            if (this.loginData.password != this.loginData.password2) {
                this.errorMsg[1].status2 = true;
            } else {
                this.errorMsg[1].status2 = false;
            }
        }
    },
};
</script>

