import axios from '@/utils/request.js';

export function getArticles(offset, size) {
    return axios({
        method: 'get',
        url: `/article?offset=${offset}&size=${size}`
    })
}

export function getSingleArticle(id) {
    return axios({
        method: 'get',
        url: `/article/${id}`
    })
}

export function getMomentAll(id) {
    return axios({
        method: 'get',
        url: `/moment/all/${id}`
    })
}

export function postMoment(data) {
    return axios({
        method: 'post',
        url: `/moment`,
        data
    })
}