<template>
    <div class="detail">
        <div class="container" v-for="(list, index) in article" :key="index">
            <div id="detail-header" class="detail-header">
                <p class="art-title">{{ list.title }}</p>
                <!-- <ul class="art-about">
                    <li>{{ list.createAt | mktime }}</li>
                    <li>阅读 {{ list.views }}</li>
                    <li>喜欢 {{ list.likes }}</li>
                </ul> -->
            </div>
            <div id="tag-box" class="tag-box">
                <span
                    class="el-tag el-tag--success el-tag--small el-tag--light"
                    >{{ list.category1 }}</span
                >
                <span
                    class="el-tag el-tag--danger el-tag--small el-tag--light"
                    >{{ list.category2 }}</span
                >
            </div>
            <div class="markdown-body">
                <div
                    id="art-body"
                    class="art-body article_message hljs renderNav"
                    v-html="list.content"
                ></div>
            </div>

            <div class="compile">
                <textarea
                    v-model="contentMoment"
                    placeholder="說點什麼吧..."
                    name=""
                    id=""
                    cols="30"
                    rows="10"
                ></textarea>
                <button class="comment" @click="submitMoment">提交評論</button>
            </div>
            <div class="leave-item" v-for="(m, index) in moment" :key="m.id">
                <div class="item-top">
                    <div class="item-left">
                        <div class="left-img">
                            <!-- <img src="https://source.unsplash.com/user/erondu/35x35" alt="" /> -->
                            <img :src="'/imgs/article/moment/'+Math.floor(Math.random()*10)+'.png'" alt="">
                        </div>
                        <p class="left-name">{{ m.username }}</p>
                    </div>
                    <div class="item-right">
                        <p>{{ index + 1 + "樓" }} {{ m.createAt | moment }}</p>
                    </div>
                </div>
                <div class="item-bottom">
                    <div class="bottom-left">
                        {{ m.content }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import marked from "marked";
import hljs from "highlight.js";
import javascript from "highlight.js/lib/languages/javascript";
import "highlight.js/styles/hybrid.css";
// import "highlight.js/styles/devibeans.css";
import { getSingleArticle, getMomentAll, postMoment } from "@/api/http";
export default {
    data() {
        return {
            id: this.$route.params.id,
            article: [],
            content: "",
            moment: null,
            contentMoment: ''
        };
    },
    async created() {
        let res = await getSingleArticle(this.id);
        this.content = marked(res.data[0].content);
        res.data[0].content = marked(res.data[0].content);
        this.article = res.data;

        let moment = await getMomentAll(this.id);
        this.moment = moment.data;
    },
    mounted() {
        marked.setOptions({
            renderer: new marked.Renderer(),
            highlight: function(code) {
                return hljs.highlightAuto(code).value;
            },
            pedantic: false,
            gfm: true,
            tables: true,
            breaks: false,
            sanitize: false,
            smartLists: true,
            smartypants: false,
            xhtml: false,
        });
    },
    methods: {
      async submitMoment() {
        if(!this.contentMoment) {
          this.$message.error('内容不能为空~~');
        } else if (!this.$store.state.token) {
          this.$message.error('请先去登陆再来留言哦,(ノへ￣、)！');
        }else {
          let data = {
            content: this.contentMoment,
            username: this.$store.state.username,
            articleId: this.id
          }
          await postMoment(data);
          this.$message.success('发表评论成功~~');
          setInterval(() => {
            location.reload()
          }, 2000)
        }
      }
    }
};
</script>

