import axios from 'axios';
import {getToken} from '@/utils/token.js';
import { Loading } from 'element-ui';

let loadingCount = 0;
let loading;

const startLoading = () => {
    loading = Loading.service({
        lock: true,
        text: '拼命加載中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0,0,0,.4)'
    });
}

const endLoading = () => {
    loading.close();
}

const showLoading = () => {
    if(loadingCount === 0) {
        startLoading();
    }
    loadingCount += 1;
}

const hideLoading = () => {
    if(loadingCount <= 0) {
        return;
    }
    loadingCount -= 1;
    if(loadingCount === 0) {
        endLoading();
    }
}

axios.defaults.baseURL = `http://localhost:8000/`;

axios.interceptors.request.use(config => {
    config.headers['Authorization'] = 'Bearer ' + localStorage.token;
    showLoading();
    return config;
}, err => {
    console.log(err);
})

axios.interceptors.response.use(res => {
    const {status, data, headers, config, error} = res;

    if(status >= 200 && status < 300 || status === 304) {
        hideLoading();
        return {data, headers};
    } else {
        throw res;
    }
}, err => {
    console.log(err);
})

export default axios;

